<template>
  <div class="textBoxSet" style="width:10%">
    <div style="text-align:left;margin-left:10%">
      <div class="addBtn" v-on:click="addTextBox"></div>
      <div id="subBtn" class="subBtn" v-on:click="delTextBox"></div>
    </div>
    <div style="text-align:left;width:100%">
      <table style="width:100%" id="textArea_1">
        <tr id="sample" style="display:none">
          <td style="width:50%;"><input type="text" maxlength="6" style="width:100%;transform:scale(0.8);"
              onkeyup="value=value.replace(/^\D*(\d*(?:\.\d{0,3})?).*$/g, '$1')"></td>
          <td style="width:50%"><input type=" text" maxlength="6" style="width:100%;transform:scale(0.8);"
              onkeyup="value=value.replace(/^\D*(\d*(?:\.\d{0,3})?).*$/g, '$1')"></td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'textBoxSet',
  components: {},
  data () {
    return {
      count: 0,
      name: 'textArea_1'
    }
  },
  computed: {

  },
  created () {
  },
  mounted () {
  },
  methods: {
    addTextBox () {
      if (this.count >= 5) { return }
      let dom = document.getElementById('sample').cloneNode(true)
      document.getElementById(this.name).appendChild(dom)
      dom.style.display = 'block'
      let idName = this.name + '_' + ++this.count
      dom.id = idName
    },
    delTextBox () {
      if (this.count <= 0) { return }
      let idName = this.name + '_' + this.count--
      let dom = document.getElementById(idName)
      dom && dom.parentNode.removeChild(dom)
    }
  }
}
</script>

<style scoped lang="less">
.textBox-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 99%;
  height: 99%;
  border: 2px solid #000;
}
.addBtn {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #d4d7d7;
  position: relative;
  cursor: pointer;
  &:hover {
    // hover状态
    background-color: #00c8c8;
  }
  &.disabledBtn {
    // 禁用状态
    background-color: #e4e4e4;
    cursor: not-allowed;
  }
}
.addBtn::before {
  // 加号的横线
  content: "";
  width: 50%;
  height: 10%;
  background-color: #fff;
  position: absolute;
  top: 45%;
  left: 25%;
}
.addBtn::after {
  // 加号的竖线
  content: "";
  width: 12%;
  height: 55%;
  background-color: #fff;
  position: absolute;
  top: 25%;
  left: 45%;
}
.subBtn {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #d4d7d7;
  position: relative;
  cursor: pointer;
  &:hover {
    background-color: #00c8c8;
  }
  &.disabledBtn {
    background-color: #e4e4e4;
    cursor: not-allowed;
  }
}
.subBtn::before {
  content: "";
  width: 50%;
  height: 10%;
  background-color: #fff;
  position: absolute;
  top: 45%;
  left: 25%;
}
</style>



